'use client';

import { useMemo, useState } from 'react';
import { mockVehicles } from '@/data/mockData';
import { VehicleCard } from '@/components/VehicleCard';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import {
  Search,
  Filter,
  Car,
  AlertTriangle,
  CheckCircle,
  Wrench
} from 'lucide-react';
import Link from 'next/link';

type VehicleTypeFilter = 'all' | 'car' | 'ambulance' | 'helicopter';
type VehicleStatusFilter = 'all' | 'active' | 'maintenance' | 'out_of_service';

export default function VehiclesPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const [typeFilter, setTypeFilter] = useState<VehicleTypeFilter>('all');
  const [statusFilter, setStatusFilter] = useState<VehicleStatusFilter>('all');

  const filteredVehicles = useMemo(() => {
    return mockVehicles.filter(vehicle => {
      const matchesSearch = searchTerm.trim() === '' ||
        vehicle.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        vehicle.model.toLowerCase().includes(searchTerm.toLowerCase()) ||
        (vehicle.licensePlate?.toLowerCase().includes(searchTerm.toLowerCase()) ?? false) ||
        vehicle.registrationNumber.toLowerCase().includes(searchTerm.toLowerCase());

      const matchesType = typeFilter === 'all' || vehicle.type === typeFilter;
      const matchesStatus = statusFilter === 'all' || vehicle.status === statusFilter;

      return matchesSearch && matchesType && matchesStatus;
    });
  }, [searchTerm, typeFilter, statusFilter]);

  const totalVehicles = mockVehicles.length;
  const activeVehicles = mockVehicles.filter(v => v.status === 'active').length;
  const maintenanceVehicles = mockVehicles.filter(v => v.status === 'maintenance').length;

  const totalDevices = mockVehicles.reduce((acc, vehicle) => acc + vehicle.devices.length, 0);
  const overdueDevices = mockVehicles.reduce((acc, vehicle) =>
    acc + vehicle.devices.filter(d => d.status === 'overdue').length, 0
  );
  const dueSoonDevices = mockVehicles.reduce((acc, vehicle) =>
    acc + vehicle.devices.filter(d => d.status === 'due').length, 0
  );

  const clearFilters = () => {
    setSearchTerm('');
    setTypeFilter('all');
    setStatusFilter('all');
  };

  return (
    <div className="min-h-screen fade-in">
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-white">Fleet Management</h1>
              <p className="mt-1 text-sm text-slate-300">
                Manage and track all vehicles and their equipment status
              </p>
            </div>
            <Link href="/notifications">
              <Button variant="outline" size="sm">
                <AlertTriangle className="h-4 w-4 mr-2" />
                Alerts ({overdueDevices + dueSoonDevices})
              </Button>
            </Link>
          </div>
        </div>

        {/* Summary Cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Total Vehicles</CardTitle>
              <Car className="h-4 w-4 text-slate-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-white">{totalVehicles}</div>
              <p className="text-xs text-slate-300">
                {mockVehicles.filter(v => v.type === 'car').length} cars • {mockVehicles.filter(v => v.type === 'ambulance').length} ambulances • {mockVehicles.filter(v => v.type === 'helicopter').length} helicopters
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Active Fleet</CardTitle>
              <CheckCircle className="h-4 w-4 text-emerald-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-emerald-200">{activeVehicles}</div>
              <p className="text-xs text-slate-300">
                {maintenanceVehicles} in maintenance
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Total Devices</CardTitle>
              <Wrench className="h-4 w-4 text-slate-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-white">{totalDevices}</div>
              <p className="text-xs text-slate-300">Across all vehicles</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Maintenance Alerts</CardTitle>
              <AlertTriangle className="h-4 w-4 text-red-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-200">{overdueDevices + dueSoonDevices}</div>
              <p className="text-xs text-slate-300">
                {overdueDevices} overdue, {dueSoonDevices} due soon
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Filters */}
        <Card className="mb-8">
          <CardContent className="p-6">
            <div className="flex items-center gap-2 mb-4">
              <Filter className="h-5 w-5 text-slate-300" />
              <h3 className="font-semibold text-white">Filter Vehicles</h3>
              {(searchTerm || typeFilter !== 'all' || statusFilter !== 'all') && (
                <Button size="sm" variant="outline" onClick={clearFilters}>
                  Clear Filters
                </Button>
              )}
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="relative">
                <Search className="absolute left-3 top-3 h-4 w-4 text-slate-400" />
                <input
                  type="text"
                  placeholder="Search by name, model, plate..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full pl-10 pr-4 py-2 border border-white/20 rounded-md text-sm bg-white/10 text-white placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-cyan-400/60"
                />
              </div>

              <select
                value={typeFilter}
                onChange={(e) => setTypeFilter(e.target.value as VehicleTypeFilter)}
                className="w-full px-4 py-2 border border-white/20 rounded-md text-sm bg-white/10 text-white focus:outline-none focus:ring-2 focus:ring-cyan-400/60"
              >
                <option value="all">All Types</option>
                <option value="car">Cars</option>
                <option value="ambulance">Ambulances</option>
                <option value="helicopter">Helicopters</option>
              </select>

              <select
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value as VehicleStatusFilter)}
                className="w-full px-4 py-2 border border-white/20 rounded-md text-sm bg-white/10 text-white focus:outline-none focus:ring-2 focus:ring-cyan-400/60"
              >
                <option value="all">All Status</option>
                <option value="active">Active</option>
                <option value="maintenance">Maintenance</option>
                <option value="out_of_service">Out of Service</option>
              </select>
            </div>
          </CardContent>
        </Card>

        {/* Fleet Grid */}
        <div>
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-white">
              Vehicle List ({filteredVehicles.length})
            </h2>
            <div className="flex gap-2">
              {typeFilter !== 'all' && (
                <Badge variant="secondary">{typeFilter}</Badge>
              )}
              {statusFilter !== 'all' && (
                <Badge variant="secondary">{statusFilter.replace('_', ' ')}</Badge>
              )}
            </div>
          </div>

          {filteredVehicles.length === 0 ? (
            <Card>
              <CardContent className="p-8 text-center">
                <div className="text-slate-300">
                  No vehicles match your current filters.
                </div>
              </CardContent>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
              {filteredVehicles.map((vehicle) => (
                <VehicleCard key={vehicle.id} vehicle={vehicle} />
              ))}
            </div>
          )}
        </div>
      </main>
    </div>
  );
}
