'use client';

import { Vehicle, MaintenanceStatus } from '@/types';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import {
  Car,
  Ambulance,
  Helicopter,
  AlertTriangle,
  CheckCircle,
  Clock,
  MapPin,
  Users
} from 'lucide-react';
import Link from 'next/link';

interface VehicleCardProps {
  vehicle: Vehicle;
}

export function VehicleCard({ vehicle }: VehicleCardProps) {
  // Calculate maintenance status summary
  const deviceStats = vehicle.devices.reduce((acc, device) => {
    acc[device.status] = (acc[device.status] || 0) + 1;
    return acc;
  }, {} as Record<MaintenanceStatus, number>);

  const totalDevices = vehicle.devices.length;
  const overdueCount = deviceStats.overdue || 0;
  const dueCount = deviceStats.due || 0;
  const upcomingCount = deviceStats.upcoming || 0;
  const completedCount = deviceStats.completed || 0;

  const healthPercentage = Math.round(((totalDevices - overdueCount - dueCount) / totalDevices) * 100);

  const getStatusColor = (status: Vehicle['status']) => {
    switch (status) {
      case 'active': return 'bg-emerald-500/20 text-emerald-200 border-emerald-400/30';
      case 'maintenance': return 'bg-amber-500/20 text-amber-200 border-amber-400/30';
      case 'out_of_service': return 'bg-red-500/20 text-red-200 border-red-400/30';
      default: return 'bg-slate-500/20 text-slate-200 border-slate-400/30';
    }
  };

  const getVehicleIcon = (type: Vehicle['type']) => {
    switch (type) {
      case 'car': return <Car className="h-6 w-6" />;
      case 'ambulance': return <Ambulance className="h-6 w-6" />;
      case 'helicopter': return <Helicopter className="h-6 w-6" />;
    }
  };

  const getHealthColor = (percentage: number) => {
    if (percentage >= 80) return 'text-emerald-200';
    if (percentage >= 60) return 'text-amber-200';
    return 'text-red-200';
  };

  return (
    <Link href={`/vehicle/${vehicle.id}`} className="block">
      <Card className="hover:shadow-2xl hover:scale-[1.02] transition-all duration-300 cursor-pointer border-l-4 border-l-transparent hover:border-l-cyan-400/60">
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="p-2 rounded-xl bg-gradient-to-br from-blue-500/30 via-cyan-400/20 to-emerald-400/20 text-cyan-100 shadow-lg shadow-blue-500/20">
              {getVehicleIcon(vehicle.type)}
            </div>
            <div>
              <CardTitle className="text-lg text-white">{vehicle.name}</CardTitle>
              <p className="text-sm text-slate-300">{vehicle.model}</p>
            </div>
          </div>
          <Badge className={`${getStatusColor(vehicle.status)} border`}>
            {vehicle.status.replace('_', ' ')}
          </Badge>
        </div>
      </CardHeader>

      <CardContent className="space-y-4">
        {/* Health Overview */}
        <div className="space-y-2">
          <div className="flex items-center justify-between text-sm text-slate-200">
            <span className="font-medium">Maintenance Health</span>
            <span className={`font-semibold ${getHealthColor(healthPercentage)}`}>
              {healthPercentage}%
            </span>
          </div>
          <Progress value={healthPercentage} className="h-2" />
        </div>

        {/* Device Status Summary */}
        <div className="grid grid-cols-2 gap-4 text-sm text-slate-200">
          <div className="space-y-1">
            <div className="flex items-center gap-1 text-red-200">
              <AlertTriangle className="h-4 w-4" />
              <span>Overdue: {overdueCount}</span>
            </div>
            <div className="flex items-center gap-1 text-amber-200">
              <Clock className="h-4 w-4" />
              <span>Due Soon: {dueCount}</span>
            </div>
          </div>
          <div className="space-y-1">
            <div className="flex items-center gap-1 text-cyan-200">
              <Clock className="h-4 w-4" />
              <span>Upcoming: {upcomingCount}</span>
            </div>
            <div className="flex items-center gap-1 text-emerald-200">
              <CheckCircle className="h-4 w-4" />
              <span>Completed: {completedCount}</span>
            </div>
          </div>
        </div>

        {/* Vehicle Details */}
        <div className="pt-2 border-t border-white/10 space-y-2">
          <div className="flex items-center gap-2 text-sm text-slate-300">
            <MapPin className="h-4 w-4" />
            <span>{vehicle.location}</span>
          </div>

          <div className="flex items-center gap-2 text-sm text-slate-300">
            <Users className="h-4 w-4" />
            <span>{vehicle.assignedContacts.length} assigned contacts</span>
          </div>

          {vehicle.mileage && (
            <div className="text-sm text-slate-300">
              Mileage: {vehicle.mileage.toLocaleString()} km
            </div>
          )}

          {vehicle.flightHours && (
            <div className="text-sm text-slate-300">
              Flight Hours: {vehicle.flightHours.toLocaleString()} hrs
            </div>
          )}

          <div className="text-xs text-slate-400">
            Next Inspection: {vehicle.nextInspectionDate.toLocaleDateString()}
          </div>
        </div>
      </CardContent>
    </Card>
    </Link>
  );
}